/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/

/* Buttons */

button,
input[type='button'],
input[type='reset'],
input[type='submit'] {
  border: none;
  background: #248a83;
  color: #fff;
  &:hover {
    background-color: #1c6f69;
  }
  @include font-size(0.9);
  line-height: 1;
  padding: 0.6rem 1rem 0.6rem;
  font-family: 'Novecento Sans Web';

  /* Fields */

  input[type='text'],
  input[type='email'],
  input[type='url'],
  input[type='password'],
  input[type='search'],
  textarea {
    color: #666;
    border: 1px solid #ccc;
    border-radius: 3px;

    &:focus {
      color: #111;
    }
  }

  input[type='text'],
  input[type='email'],
  input[type='url'],
  input[type='password'],
  input[type='search'] {
    padding: 3px;
  }

  textarea {
    padding-left: 3px;
    width: 100%;
  }
}
